.aside-menu-products {
	h2 {
		font-size: 2.4rem;
		position: relative;
		margin-bottom: 1.6rem;
	}
	ul {
		li {
			+ li {
				margin-top: 1.6rem;
			}
			span {
				margin-left: 0.8rem;
			}
			> a {
				position: relative;
				padding-left: 2.4rem;
				transition: 0.3s all ease-in-out;
				&:before {
					content: '\f054';
					position: absolute;
					left: 0;
					top: 50%;
					transform: translateY(-50%);
					font-family: $hvh-font-icons;
				}
				span {
					margin-left: 0;
					+ span {
						margin-left: 0.8rem;
					}
				}
			}
			@media (min-width: 1025px) {
				&:hover {
					> a {
						color: $hvh-primary-color;
					}
				}
			}
		}
	}
	.filter-block {
		+ .filter-block {
			margin-top: 4.4rem;
			@media (max-width: 767.98px) {
				margin-top: 2.4rem;
			}
		}
	}
	.fiiter-submit {
		position: fixed;
		display: flex;
		left: 0;
		bottom: 0;
		width: 100%;
		background: $hvh-white-color;
		padding: 1.6rem;
		box-shadow: 0 0.3rem 0.6rem rgba($color: #000000, $alpha: 0.16);
		.btn + .btn {
			margin-left: 2.4rem;
		}
		@media (min-width: 1025px) {
			display: none;
		}
	}
	.filter-wrapper {
		@media (max-width: 1024.98px) {
			height: calc(100vh - 8.6rem - 2.4rem);
			overflow-y: auto;
		}
	}
	@media (max-width: 1024.98px) {
		position: fixed;
		left: 0;
		top: 0;
		width: 60%;
		height: 100vh;
		max-height: 100%;
		overflow: auto;
		padding: 2.4rem;
		background: $hvh-white-color;
		z-index: 600;
		//HIDDEN
		transform: translateX(-100%);
		transition: transform 500ms cubic-bezier(1, 0, 0, 1) 0ms,
			left 500ms cubic-bezier(1, 0, 0, 1) 0ms;
		&.active {
			transform: translateX(0);
		}
	}
	@media (max-width: 575.98px) {
		padding: 1.6rem;
		width: 80%;
		.filter-wrapper {
			height: calc(100vh - 7.6rem - 2.4rem);
		}
	}
}

.products {
	.item-product {
		margin-bottom: 2.4rem;
	}
	.collection-bar {
		margin-bottom: 4.4rem;
		.collection-toolbar {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			@media (max-width: 575.98px) {
				flex-direction: column;
				align-items: flex-start;
			}
		}
		.toolbar-left {
			font-size: 1.4rem;
			display: flex;
			align-items: center;
			@media (min-width: 1025px) {
				display: none;
			}
		}
		.toolbar-right {
			margin-left: auto;
			@media (max-width: 575.98px) {
				margin-left: 0;
				margin-top: 1.6rem;
			}
		}
		@media (max-width: 767.98px) {
			margin-bottom: 2.4rem;
		}
	}
	.list-product {
		position: relative;
		&:after {
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			background: $hvh-gray-color;
			top: 0;
			left: 0;
			z-index: 10;
			opacity: 0;
			visibility: hidden;
			cursor: not-allowed;
			transition: 0.3s all ease-in-out;
		}
		&.loading {
			&:after {
				opacity: 0.5;
				visibility: visible;
			}
		}
	}
}
